/**
 * メニュー管理システム メインアプリケーション
 * 主な仕様：
 * - ルーティング制御
 * - 認証状態管理
 * - レイアウト管理
 * - GitHub Pages + ローカルバックエンド対応
 * - 設計書に従ったダッシュボード実装
 * 制限事項：
 * - シンプルなローカル状態管理（大規模時はReduxなど検討）
 */

import React, { useState, useEffect } from 'react';
import { Layout, Menu, Typography, Card, Row, Col, Table, Button, Space, Breadcrumb } from 'antd';
import { 
  DashboardOutlined, 
  InboxOutlined, 
  MenuOutlined, 
  UserOutlined,
  LogoutOutlined,
  PlusOutlined,
  EditOutlined,
  CheckOutlined,
  CloseOutlined
} from '@ant-design/icons';
import { MaterialsAPI, MenusAPI, HealthAPI } from './services/api';

const { Header, Content, Sider } = Layout;
const { Title, Text } = Typography;

function App() {
  const [selectedMenuItem, setSelectedMenuItem] = useState('dashboard');
  const [materials, setMaterials] = useState([]);
  const [menus, setMenus] = useState([]);
  const [recentMenus, setRecentMenus] = useState([]);
  const [loading, setLoading] = useState(false);

  // 初期データ取得
  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    setLoading(true);
    try {
      // 材料データ取得
      const materialsData = await MaterialsAPI.getAll();
      setMaterials(materialsData);

      // メニューデータ取得
      const menusData = await MenusAPI.getAll();
      setMenus(menusData);
      setRecentMenus(menusData.slice(0, 5)); // 最新5件
    } catch (error) {
      console.error('データ取得エラー:', error);
    }
    setLoading(false);
  };

  // ダッシュボード画面
  const renderDashboard = () => {
    // 新規承認待ちメニュー一覧のテーブル定義
    const pendingMenuColumns = [
      {
        title: 'ID',
        dataIndex: 'id',
        key: 'id',
        width: 80,
      },
      {
        title: 'メニュー名',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: '登録者',
        dataIndex: 'creator',
        key: 'creator',
        render: () => '店舗A', // 仮データ
      },
      {
        title: '登録日',
        dataIndex: 'last_updated',
        key: 'last_updated',
        render: (date) => new Date(date).toLocaleDateString('ja-JP'),
      },
      {
        title: '状態',
        dataIndex: 'status',
        key: 'status',
        render: () => '承認待ち', // 仮データ
      },
      {
        title: '操作',
        key: 'actions',
        render: (_, record) => (
          <Space>
            <Button type="primary" size="small" icon={<CheckOutlined />}>
              承認
            </Button>
            <Button size="small" icon={<CloseOutlined />}>
              差戻し
            </Button>
          </Space>
        ),
      },
    ];

    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item>ダッシュボード</Breadcrumb.Item>
        </Breadcrumb>

        <Row gutter={[16, 16]}>
          {/* 統計情報カード */}
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}>登録材料数</Title>
              <Title level={2} type="primary">{materials.length}</Title>
            </Card>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}>登録メニュー数</Title>
              <Title level={2} type="success">{menus.length}</Title>
            </Card>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}>承認待ち</Title>
              <Title level={2} type="warning">0</Title>
            </Card>
          </Col>
          <Col xs={24} sm={12} md={6}>
            <Card>
              <Title level={4}>今月更新</Title>
              <Title level={2}>{recentMenus.length}</Title>
            </Card>
          </Col>
        </Row>

        <Row gutter={[16, 16]} style={{ marginTop: 24 }}>
          {/* 新規承認待ちメニュー一覧 */}
          <Col xs={24} lg={16}>
            <Card 
              title="新規承認待ちメニュー一覧"
              extra={<Button type="primary" icon={<PlusOutlined />}>新規作成</Button>}
            >
              <Table
                columns={pendingMenuColumns}
                dataSource={menus.map(menu => ({ ...menu, key: menu.id }))}
                pagination={{ pageSize: 5 }}
                loading={loading}
              />
            </Card>
          </Col>

          {/* 最近更新されたメニュー */}
          <Col xs={24} lg={8}>
            <Card title="最近更新されたメニュー">
              {recentMenus.map(menu => (
                <div key={menu.id} style={{ marginBottom: 16, padding: 12, backgroundColor: '#f5f5f5', borderRadius: 6 }}>
                  <Text strong>{menu.name}</Text>
                  <br />
                  <Text type="secondary" style={{ fontSize: '12px' }}>
                    更新日: {new Date(menu.last_updated).toLocaleDateString('ja-JP')}
                  </Text>
                  <br />
                  <Button type="link" size="small" icon={<EditOutlined />}>編集</Button>
                </div>
              ))}
            </Card>
          </Col>
        </Row>
      </div>
    );
  };

  // 材料マスタ管理画面
  const renderMaterials = () => {
    const materialColumns = [
      {
        title: '材料コード',
        dataIndex: 'id',
        key: 'id',
        width: 100,
      },
      {
        title: '材料名',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: '単価',
        dataIndex: 'unit_price',
        key: 'unit_price',
        render: (price) => `${price}円`,
      },
      {
        title: '単位',
        dataIndex: 'unit',
        key: 'unit',
      },
      {
        title: 'アレルギー',
        dataIndex: 'allergy_info',
        key: 'allergy_info',
        render: (allergy) => allergy || 'なし',
      },
      {
        title: '原産国',
        dataIndex: 'origin_country',
        key: 'origin_country',
      },
      {
        title: '操作',
        key: 'actions',
        render: (_, record) => (
          <Button type="link" icon={<EditOutlined />}>編集</Button>
        ),
      },
    ];

    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item>材料マスタ管理</Breadcrumb.Item>
        </Breadcrumb>

        <Card 
          title="材料マスタ管理"
          extra={
            <Space>
              <Button type="primary" icon={<PlusOutlined />}>新規登録</Button>
              <Button>検索</Button>
            </Space>
          }
        >
          <Table
            columns={materialColumns}
            dataSource={materials.map(material => ({ ...material, key: material.id }))}
            loading={loading}
            pagination={{ 
              showSizeChanger: true,
              showQuickJumper: true,
              showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`
            }}
          />
        </Card>
      </div>
    );
  };

  // メニュー一覧画面
  const renderMenus = () => {
    const menuColumns = [
      {
        title: 'メニューコード',
        dataIndex: 'id',
        key: 'id',
        width: 120,
        render: (id) => `${id}-A`,
      },
      {
        title: 'メニュー名',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: '販売価格',
        dataIndex: 'selling_price',
        key: 'selling_price',
        render: (price) => `${price}円`,
      },
      {
        title: '原価率',
        key: 'cost_rate',
        render: () => '28%', // 仮データ
      },
      {
        title: 'アレルギー',
        key: 'allergy',
        render: () => '小麦,卵,大豆', // 仮データ
      },
      {
        title: '操作',
        key: 'actions',
        render: (_, record) => (
          <Button type="link" icon={<EditOutlined />}>編集</Button>
        ),
      },
    ];

    return (
      <div>
        <Breadcrumb style={{ margin: '16px 0' }}>
          <Breadcrumb.Item>メニュー一覧</Breadcrumb.Item>
        </Breadcrumb>

        <Card 
          title="メニュー一覧"
          extra={
            <Space>
              <Button type="primary" icon={<PlusOutlined />}>新規作成</Button>
              <Button>検索</Button>
            </Space>
          }
        >
          <Table
            columns={menuColumns}
            dataSource={menus.map(menu => ({ ...menu, key: menu.id }))}
            loading={loading}
            pagination={{ 
              showSizeChanger: true,
              showQuickJumper: true,
              showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`
            }}
          />
        </Card>
      </div>
    );
  };

  // コンテンツレンダリング
  const renderContent = () => {
    switch (selectedMenuItem) {
      case 'dashboard':
        return renderDashboard();
      case 'materials':
        return renderMaterials();
      case 'menus':
        return renderMenus();
      default:
        return renderDashboard();
    }
  };

  return (
    <Layout style={{ minHeight: '100vh' }}>
      <Header style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <div style={{ color: 'white', fontSize: '18px', fontWeight: 'bold' }}>
          メニュー管理システム
        </div>
        <Space style={{ color: 'white' }}>
          <UserOutlined />
          <span>管理者</span>
          <Button type="text" icon={<LogoutOutlined />} style={{ color: 'white' }}>
            ログアウト
          </Button>
        </Space>
      </Header>

      <Layout>
        <Sider width={200} style={{ background: '#fff' }}>
          <Menu
            mode="inline"
            selectedKeys={[selectedMenuItem]}
            style={{ height: '100%', borderRight: 0 }}
            items={[
              {
                key: 'dashboard',
                icon: <DashboardOutlined />,
                label: 'ダッシュボード',
                onClick: () => setSelectedMenuItem('dashboard')
              },
              {
                key: 'materials',
                icon: <InboxOutlined />,
                label: '材料マスタ管理',
                onClick: () => setSelectedMenuItem('materials')
              },
              {
                key: 'menus',
                icon: <MenuOutlined />,
                label: 'メニュー管理',
                onClick: () => setSelectedMenuItem('menus')
              },
              {
                key: 'users',
                icon: <UserOutlined />,
                label: 'ユーザー管理',
                onClick: () => setSelectedMenuItem('users')
              }
            ]}
          />
        </Sider>

        <Layout style={{ padding: '0 24px 24px' }}>
          <Content
            style={{
              padding: 24,
              margin: 0,
              minHeight: 280,
              background: '#fff',
              borderRadius: 6,
            }}
          >
            {renderContent()}
          </Content>
        </Layout>
      </Layout>
    </Layout>
  );
}

export default App; 