/**
 * APIサービス
 * 主な仕様：
 * - バックエンドAPIとの通信
 * - 材料、メニュー、ユーザー管理のAPI呼び出し
 * - エラーハンドリング
 * - JWT認証対応
 * 制限事項：
 * - ローカル開発環境用（本番ではURLを適切に設定）
 */

import axios from 'axios';

// APIベースURL設定
const API_BASE_URL = window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1' 
  ? 'http://localhost:8000/api/v1'
  : 'http://localhost:8000/api/v1'; // 本番環境では適切なURLに変更

// Axiosインスタンス作成
const apiClient = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// リクエストインターセプター（認証トークン付与）
apiClient.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('auth_token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// レスポンスインターセプター（エラーハンドリング）
apiClient.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    if (error.response?.status === 401) {
      // 認証エラーの場合、トークンを削除してログイン画面へ
      localStorage.removeItem('auth_token');
      window.location.href = '/login';
    }
    return Promise.reject(error);
  }
);

/**
 * 材料管理API
 */
export class MaterialsAPI {
  /**
   * 材料一覧取得
   * @returns {Promise<Array>} 材料データ配列
   */
  static async getAll() {
    try {
      const response = await apiClient.get('/materials');
      return response.data;
    } catch (error) {
      console.error('材料一覧取得エラー:', error);
      throw error;
    }
  }

  /**
   * 材料詳細取得
   * @param {number} id 材料ID
   * @returns {Promise<Object>} 材料データ
   */
  static async getById(id) {
    try {
      const response = await apiClient.get(`/materials/${id}`);
      return response.data;
    } catch (error) {
      console.error('材料詳細取得エラー:', error);
      throw error;
    }
  }

  /**
   * 材料作成
   * @param {Object} materialData 材料データ
   * @returns {Promise<Object>} 作成された材料データ
   */
  static async create(materialData) {
    try {
      const response = await apiClient.post('/materials', materialData);
      return response.data;
    } catch (error) {
      console.error('材料作成エラー:', error);
      throw error;
    }
  }

  /**
   * 材料更新
   * @param {number} id 材料ID
   * @param {Object} materialData 更新する材料データ
   * @returns {Promise<Object>} 更新された材料データ
   */
  static async update(id, materialData) {
    try {
      const response = await apiClient.put(`/materials/${id}`, materialData);
      return response.data;
    } catch (error) {
      console.error('材料更新エラー:', error);
      throw error;
    }
  }

  /**
   * 材料削除
   * @param {number} id 材料ID
   * @returns {Promise<void>}
   */
  static async delete(id) {
    try {
      await apiClient.delete(`/materials/${id}`);
    } catch (error) {
      console.error('材料削除エラー:', error);
      throw error;
    }
  }
}

